import React, { useEffect, useState } from "react";
import GoogleTrends from "./GoogleTrends";
import "./styles.css";

export default function App() {
  const [term, setTerm] = useState("Pillows for hamsters");
  const [compare, setCompare] = useState("Pillows");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 500);
  }, []);

  const loadData = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  };

  return (
    <>
      <h1> Google Trends Integration</h1>

      <div>
        <label>Solution</label>
        <br></br>
        <input
          name="term"
          onChange={(e) => {
            setTerm(e.target.value);
          }}
          value={term}
        />
      </div>
      <br></br>
      <div>
        <label>Alternative</label>
        <br></br>
        <input
          name="alternate"
          onChange={(e) => {
            setCompare(e.target.value);
          }}
          value={compare}
        />
      </div>
      <br></br>
      <div
        onClick={() => loadData()}
        style={{
          padding: "10px",
          background: "gray",
          color: "white",
          width: "100px",
          textAlign: "center",
          cursor: "pointer"
        }}
      >
        {loading ? "Loading...." : "Search"}
      </div>
      <br></br>
      {!loading ? (
        <div id="widget">
          <GoogleTrends
            type="TIMESERIES"
            keyword={term}
            compareTo={compare}
            url="https://ssl.gstatic.com/trends_nrtr/2965_RC01/embed_loader.js"
          />
          <GoogleTrends
            type="GEO_MAP"
            keyword={term}
            compareTo={compare}
            url="https://ssl.gstatic.com/trends_nrtr/2965_RC01/embed_loader.js"
          />
        </div>
      ) : (
        <div>...Loading</div>
      )}
    </>
  );
}
