import React from "react";
import ReactDOM from "react-dom";
import Script from "react-load-script";

export default function GoogleTrends({ type, keyword, compareTo, url }) {
  const handleScriptLoad = (_) => {
    window.trends.embed.renderExploreWidgetTo(
      document.getElementById("widget"),
      type,
      {
        comparisonItem: [
          { keyword, geo: "US", time: "today 12-m" },
          { keyword: compareTo, geo: "US", time: "today 12-m" }
        ],
        category: 0,
        property: ""
      },
      {
        exploreQuery: `q=${encodeURI(keyword)},${encodeURI(
          compareTo
        )}&geo=US&date=today 12-m`,
        guestPath: "https://trends.google.com:443/trends/embed/"
      }
    );
  };

  const renderGoogleTrend = (_) => {
    return <Script url={url} onLoad={handleScriptLoad} />;
  };

  return <div className="googleTrend">{renderGoogleTrend()}</div>;
}

